.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 20px 17px;
  background: transparent;
  color: white;
}
.navbar img {
  max-width: 50px;
  max-height: 50px;
  border: 1px solid black;
  border-radius: 15px 10px 15px 10px;
}
.nav-menu {
  display: flex;
  align-items: center;
  list-style: none;
  gap: 60px;
  font-family: "Roboto", sans-serif;
  font-size: 17px;
  cursor: pointer;
}
.nav-menu li:hover {
  color: orange;
  transform: scale(1.04);
}

.nav-connect {
  padding: 20px 20px;
  border-radius: 50px;
  background: #1d976c; /* fallback for old browsers */
  background: linear-gradient(90deg, #12c2e9, #c471ed, #f64f59);
  font-family: "Roboto", sans-serif;
  font-size: large;
  font-weight: 400;
  font-style: normal;
  cursor: pointer;
}

.nav-connect:hover {
  transform: scale(1.05);
  color: black;
}
