.about {
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 80px;
    margin: 80px 170px;
}

.about-title {
    position: relative;
}

.about-title h1 {
    padding: 0px 30px;
    font-size: 50px;
    font-weight: 600px;
}
.about-title img {
    position: absolute;
    bottom: 0 ;
    right: 0;
    z-index: -1;
}

.about-sections {
    display: flex;
    gap: 80px;
}

.about-right {
    display: flex;
    flex-direction: column;
    gap: 50px;
}

.about-left img{
max-width: 360px;
max-height: 360px;
border-radius: 20px;
border: 1px solid white;
}


.about-para {
    display: flex;
    flex-direction: column;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    gap:5px;
    font-size: 20px;
    font-weight: 400;

}
.about-skills {
    display: flex;
    flex-direction: column;
    gap:20px;
}

.about-skill {
    display: flex;
    gap: 50px;
    align-items: center;
    transition: 0.3s;
}
.about-skill p {
    min-width: 150px;
    font-size: 24px;
    font-weight: 500;

}

.about-skill hr {
    outline: none;
    border: none;
    width: 50px;
    height: 8px;
    border-radius: 50px;
    background:linear-gradient(270deg, #12c2e9, #c471ed, #f64f59);
}

.about-achievements{
    display: flex;
    width: 100%;
    justify-content: space-around;
    margin-bottom: 80px;
}

.about-achievement {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    transition: 0.5s;
}

.about-achievement h1 {
    font-size: 60px;
    font-weight: 700;
    background:linear-gradient(#12c2e9, #c471ed, #f64f59);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.about-achievement p {
    font-size: 22px;
    font-weight: 500;
}

.about-skill:hover {
    transform:scale(1.12);
    transition: 0.3s;
}

.about-achievement:hover {
    transform: scale(1.12);
    transition: 0.5s;
}