.hero {
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 35px;
}

.hero img {
  border-radius: 50%;
  margin-top: 15px;
  border: 2px dotted white;
  max-height: 260px;
  max-width: 260px;
}
.hero h1 {
  text-align: center;
  width: 100%;
  font-size: 30px;
  font-family: "Roboto", sans-serif;
  font-weight: 300;
}

.hero h1 span {
  background: linear-gradient(270deg, #12c2e9, #c471ed, #f64f59);
  background-clip: text;
  -webkit-background-clip: text;
  font-size: 50px;
  font-weight: 400;
  -webkit-text-fill-color: transparent;
}

.hero p {
        width: 50%;
        text-align: center;
        font-size: 20px;
        line-height: 29px;
        font-family: 'Courier New', Courier, monospace;

}

.hero-action {
        display: flex;
        align-items: center;
        gap: 55px;
        font-size: 20px;
        font-weight: 500;
        margin-bottom: 50px;
}
.hero-connect {
        padding: 20px 40px;
        border-radius: 50px;
        background: linear-gradient(90deg,#12c2e9, #c471ed, #f64f59);
        cursor: pointer;
}

.hero-resume {
        padding: 20px 40px;
        border-radius: 50px;
        border: 2px solid #ffff;
        cursor: pointer;
}

.hero-connect:hover{
border: 2px solid white;
}

.hero-resume:hover{
border: 2px solid orange;
}
